:root {
  --flex-gap: 20px;
  --x-flex-gap: 20px;
  --y-flex-gap: 10px;
  --bgcolor: white;
  --fgcolor: black;
}

*, *::before, *::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  width: 90vw;
  min-width: 900px;
  margin: 10vh auto;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Light mode */
@media (prefers-color-scheme: light) {
  body {
    background-color: white;
    background-color: var(--bgcolor);
    color: black;
    color: var(--fgcolor);
  }
}

/* Dark mode */
@media (prefers-color-scheme: dark) {
  :root{
    --bgcolor: black;
    --fgcolor: white;
  }
  body {
    background-color: white;
    background-color: var(--bgcolor);
    color: black;
    color: var(--fgcolor);
  }
  input {
    background-color: white;
    background-color: var(--bgcolor);
    color: black;
    color: var(--fgcolor);
    border-color: black;
    border-color: var(--fgcolor);
  }
}


.flex {
    display: flex;
    grid-gap: var(--flex-gap);
    gap: var(--flex-gap);
}

.x-flex {
    flex-direction: row;
    grid-gap: var(--x-flex-gap);
    gap: var(--x-flex-gap);
}

.x-flex > div:first-child {
    min-width: 100px;
    text-align: right;
}

.y-flex {
    flex-direction: column;
    grid-gap: var(--y-flex-gap);
    gap: var(--y-flex-gap);
}

input.input {
    box-sizing: border-box;
    display: block;
    padding: 10px;
    min-width: 600px;
    width: 100%;
    font-size: 20px;
}

div {
    word-wrap: anywhere;
}

section h2.clickable {
    cursor: pointer;
}

